:root {
  --color-primary: #f36;
  --color-primary-light: #ff4171;
  --color-primary-dark: #ba265d;
  --color-secondary: #101d2c;
  --color-greylight-1: #faf9f9;
  --color-grey-light-2: #f4f2f2;
  --color-grey-light-3: #f0eeee;
  --color-grey-light-4: #ccc;
  --color-grey-dark-1: #333;
  --color-grey-dark-2: #8492a6;
  --color-grey-dark-3: #999;
  --color-black-1: #212529;
  --color-black-2: #3c4858;
  --background-color: #eaedf2;
  --shadow-dark: 0 2rem 6rem #0000004d;
  --shadow-medium: 0 2rem 6rem #00000026;
  --shadow-light: 0 2rem 5rem #0000000f;
  --line: 1px solid var(--color-grey-light-2);
}

*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  scroll-padding-top: 6.5rem;
  font-size: 62.5%;
}

@media only screen and (width <= 75em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (width <= 62.5em) {
  html {
    font-size: 45%;
  }
}

body {
  color: var(--color-secondary);
  background-color: #fff;
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  line-height: 1.6;
}

@media only screen and (width <= 75em) {
  body {
    font-size: 50%;
  }
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.container {
  grid-template-rows: 100vh repeat(5, max-content);
  grid-template-columns: [full-start] 1fr[center-start] repeat(9, minmax(min-content, 14rem))[center-end] 1fr[full-end];
  display: grid;
}

@media only screen and (width <= 75em) {
  .container {
    grid-template-rows: 80vh repeat(5, max-content);
  }
}

.section-title {
  color: var(--color-black-1);
  text-align: left;
  margin: 10rem 5rem 5rem;
  position: relative;
}

@media only screen and (width <= 35.8em) {
  .section-title {
    margin-top: 5rem;
  }
}

.section-title h1 {
  text-transform: uppercase;
  margin-bottom: 3.7rem;
  font-size: 3.4rem;
  font-weight: 700;
  display: inline-block;
  position: relative;
}

.section-title h1:after {
  content: "";
  border-bottom: 4px solid #f36;
  width: 60%;
  display: inline;
  position: absolute;
  bottom: -1rem;
  left: 0;
}

.section-title p {
  color: #3c4858;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 3.5rem;
  font-size: 1.5rem;
}

.section:not(.header, .work) {
  margin: 7rem 5rem;
  padding: 1rem;
}

@media only screen and (width <= 75em) {
  .section:not(.header, .work) {
    margin: 5rem;
  }
}

@media only screen and (width <= 50em) {
  .section:not(.header, .work) {
    margin: 2.5rem;
  }
}

.header {
  background-color: var(--background-color);
  grid-area: 1 / full-start / 2 / full-end;
  padding: 0 4rem;
  position: relative;
}

@media only screen and (width <= 35.8em) {
  .header {
    padding: 0;
  }
}

.header .navigation {
  z-index: 1999;
  width: 100%;
  padding: 1.6rem 1.5rem;
  position: relative;
  top: 0;
  left: 0;
}

@media only screen and (width <= 50em) {
  .header .navigation {
    background-color: #fff;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
  }
}

.header .container {
  width: 100%;
  max-width: 114rem;
  margin: 0 auto;
  padding: .6rem;
}

.header .nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media only screen and (width <= 1400px) {
  .header .nav {
    padding: 0 10rem;
  }
}

@media only screen and (width <= 75em) {
  .header .nav {
    padding: 0 7rem;
  }
}

@media screen and (width <= 50em) {
  .header .nav {
    padding: 1rem 5rem;
    display: block;
  }
}

@media screen and (width <= 35.8em) {
  .header .nav {
    padding: 1rem 3rem;
    display: block;
  }
}

.header .nav__header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.header .nav__logo {
  cursor: pointer;
}

.header .nav__logo h1 {
  color: var(--color-primary);
  text-transform: uppercase;
  font-size: 3rem;
  font-weight: 700;
}

@media screen and (width <= 50em) {
  .header .nav__logo {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .header .nav__logo h1 {
    font-size: 3.6rem;
  }
}

.header .nav__toggle {
  border-radius: 50px;
  padding: 1.3rem;
  display: none;
}

@media screen and (width <= 50em) {
  .header .nav__toggle {
    cursor: pointer;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 45px;
    height: 40px;
    display: flex;
  }

  .header .nav__toggle .bar {
    background-color: var(--color-primary);
    border-radius: 5px;
    width: 100%;
    height: 5px;
    transition: all .3s;
    display: block;
  }

  .header .nav__toggle #bar1 {
    transform: translateY(-4px);
  }

  .header .nav__toggle #bar3 {
    transform: translateY(4px);
  }
}

.header .nav .change #bar1 {
  transform: translateY(4px)rotateZ(-45deg);
}

.header .nav .change #bar2 {
  opacity: 0;
}

.header .nav .change #bar3 {
  transform: translateY(-6px)rotateZ(45deg);
}

.header .nav__menu {
  flex: 0 0 70%;
  justify-content: space-between;
  align-items: center;
  height: auto;
  display: flex;
}

@media screen and (width <= 50em) {
  .header .nav__menu {
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
    width: 100%;
    height: 0;
    transition: all .3s ease-in-out;
    display: flex;
    overflow: hidden;
  }

  .header .nav__list {
    background-color: #fff;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: auto;
    margin: .8rem 0;
  }

  .header .nav__list .nav__link:link, .header .nav__list .nav__link:visited {
    padding: 1.7rem 0;
    font-size: 2.3rem;
    display: inline-block;
  }

  .header .nav__list .nav__item {
    width: 100%;
  }
}

.header .nav__list, .header .nav__social {
  display: flex;
}

.header .nav__item:not(:last-child) {
  margin-right: .5rem;
}

.header .nav__link {
  color: var(--color-black-1);
  text-transform: uppercase;
  margin: 0 1rem;
  font-size: 1.5rem;
  font-weight: 500;
  transition: all .3s ease-in;
  position: relative;
}

.header .nav__link:hover {
  position: relative;
}

.header .nav__link:hover:after {
  content: "";
  background-color: var(--color-primary);
  width: 100%;
  height: .28rem;
  position: absolute;
  top: 2.9rem;
  left: 0;
}

@media screen and (width <= 50em) {
  .header .nav__link:hover:after {
    top: 5.9rem;
  }
}

.header .nav__social li svg {
  fill: var(--color-grey-dark-1);
  width: 1.6rem;
  height: 1.6rem;
}

.header .nav__social li {
  border: .7px solid var(--color-grey-dark-3);
  border-radius: .5rem;
  padding: .5rem .8rem;
  transition: all .3s ease-in-out;
}

.header .nav__social li:hover {
  border-color: var(--color-primary-light);
}

.header .nav__social li:hover svg {
  fill: var(--color-primary-light);
}

.header .nav__social li a {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header .nav__social li:not(:last-child) {
  margin-right: 1rem;
}

.header .nav .active:after {
  content: "";
  background-color: var(--color-primary);
  width: 100%;
  height: .28rem;
  position: absolute;
  top: 2.9rem;
  left: 0;
}

@media screen and (width <= 50em) {
  .header .nav .active:after {
    top: 5.9rem !important;
  }
}

.header .fix__nav {
  box-shadow: var(--shadow-light);
  z-index: 9999;
  background-color: #fff;
  width: 100%;
  transition: all .3s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
}

.header .banner {
  grid-template-columns: 2fr 1fr;
  max-width: 150rem;
  height: calc(100vh - 7.5rem);
  margin: 0 auto;
  display: grid;
}

@media only screen and (width <= 1300px) {
  .header .banner {
    max-width: 110rem;
  }
}

@media only screen and (width <= 75em) {
  .header .banner {
    max-width: 105rem;
    height: 80vh;
  }
}

@media only screen and (width <= 50em) {
  .header .banner {
    grid-template-columns: 1fr;
  }
}

.header .banner__content {
  flex-direction: column;
  justify-content: center;
  align-items: start;
  display: flex;
}

@media only screen and (width <= 50em) {
  .header .banner__content {
    justify-content: flex-start;
    align-items: center;
  }

  .header .banner__content .subtitle {
    font-size: 2rem !important;
  }

  .header .banner__content .primary {
    font-size: 5.5rem !important;
  }

  .header .banner__content .title__info {
    font-size: 2rem !important;
  }
}

.header .banner__content .subtitle {
  font-size: 1.6rem;
}

.header .banner__content .subtitle span {
  color: var(--color-primary);
  border-bottom: .3rem;
  border-bottom: 1px solid var(--color-primary);
  display: inline-block;
}

.header .banner__content .primary {
  color: var(--color-primary);
  font-size: 5rem;
  font-weight: 700;
}

@media only screen and (width <= 62.5em) {
  .header .banner__content .primary span {
    visibility: hidden;
    display: none;
  }
}

.header .banner__content .title__info {
  color: var(--color-grey-dark-2);
  margin-bottom: 2rem;
  font-size: 1.6rem;
}

.header .banner__image {
  justify-content: end;
  align-items: center;
  display: grid;
}

@media only screen and (width <= 50em) {
  .header .banner__image {
    order: -1;
    justify-content: center;
    margin-top: 14rem;
  }
}

@media (height <= 414px) and (width <= 50em) {
  .header .banner__image {
    display: none;
  }
}

.header .banner__image-container {
  width: 100%;
  max-width: 400px;
  height: auto;
}

@media only screen and (width <= 50em) {
  .header .banner__image-container {
    max-width: 300px;
  }
}

@media only screen and (width <= 35.8em) {
  .header .banner__image-container {
    max-width: 210px;
  }
}

@media only screen and (width <= 320px) {
  .header .banner__image-container {
    max-width: 150px;
  }
}

.header .banner__image img {
  width: 100%;
  max-width: 400px;
  height: auto;
}

.header .scroll {
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  bottom: 2rem;
  left: 0;
  right: 0;
}

@media only screen and (width <= 75em) {
  .header .scroll {
    display: none;
  }
}

.header .circle {
  animation: 1s infinite alternate-reverse circleAnim;
}

@keyframes circleAnim {
  from {
    z-index: 5;
    transform: translate(262px, 798px);
  }

  to {
    z-index: 5;
    transform: translate(262px, 830px);
  }
}

.about {
  grid-column: center-start / center-end;
}

.about .about-container {
  grid-gap: 10rem;
  grid-template-columns: 1fr 2fr;
  padding: 4rem;
  display: grid;
}

@media only screen and (width <= 50em) {
  .about .about-container {
    grid-gap: 5rem;
    grid-template-columns: 1fr;
    padding: 0 4rem;
  }
}

.about .about-container .about__right {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  display: flex;
}

.about .about-container .about__right h1 {
  color: var(--color-primary);
  margin-bottom: 1rem;
  font-size: 2.6rem;
  font-weight: 600;
}

.about .about-container .about__right h4 {
  align-self: stretch;
  margin-bottom: .1rem;
  font-size: 1.7rem;
  font-weight: 500;
}

.about .about-container .about__right h4 span {
  color: #f36;
  border-bottom: 1px solid #f36;
}

.about .about-container .about__right .info {
  color: #000;
  margin-bottom: 2.5rem;
  font-size: 1.7rem;
  line-height: 1.7;
}

.about .about-container .about__right .btn {
  text-decoration: none;
}

.about .about-container .about__right .btn:link, .about .about-container .about__right .btn:visited {
  color: #fff;
  background-color: #f36;
  border-radius: 1rem;
  padding: .7rem 1.8rem;
  font-size: 1.5rem;
  transition: all .3s ease-in-out;
  display: inline-block;
  box-shadow: 0 .5rem 1.5rem #0000004d;
}

.about .about-container .about__right .btn:hover {
  background-color: #f36;
}

@media only screen and (width <= 50em) {
  .about .about-container .about__right {
    box-shadow: var(--shadow-light);
    border-radius: 10px;
    align-items: center;
    margin: 0 -4rem;
    padding: 3rem;
  }

  .about .about-container .about__right h4, .about .about-container .about__right .info {
    font-size: 1.9rem;
  }

  .about .about-container .about__right #lastName {
    display: none;
  }
}

.about .about-container .about__left img {
  border: .5rem solid var(--color-grey-dark-2);
  background-clip: content-box;
  border-radius: 1rem;
  max-width: 300px;
  height: 100%;
}

@media only screen and (width <= 50em) {
  .about .about-container .about__left img {
    border-radius: 50%;
    max-width: 180px;
  }

  .about .about-container .about__left {
    text-align: center;
  }
}

.skills {
  grid-column: center-start / center-end;
}

.skills .skills-container {
  grid-gap: 2rem;
  grid-template-columns: 3fr 2fr;
  margin: 0 5rem;
  display: grid;
}

@media only screen and (width <= 50em) {
  .skills .skills-container {
    grid-template-columns: 1fr;
    margin: 0;
  }
}

.skills .skills__left {
  border-radius: 1rem;
  padding: 4rem 6rem;
  box-shadow: 0 0 1rem 1px #0000001a;
}

@media only screen and (width <= 35.8em) {
  .skills .skills__left {
    box-shadow: none;
    padding: 0 4rem;
  }
}

.skills .skills__left .skills__box h4 {
  font-size: 1.5rem;
}

.skills .skills__left .skills__box .skills__bar {
  background-color: #f36;
  height: .5rem;
}

.skills .skills__left .skills__box .skills__ilt {
  background-color: #8492a6;
  border-radius: .5rem;
  width: 100%;
  height: .5rem;
  position: relative;
}

.skills .skills__left .skills__box:not(:last-child) {
  margin-bottom: 1.5rem;
}

.skills .skills__left .skills__box span {
  font-size: 1.4rem;
  position: absolute;
  bottom: 10%;
  right: 0;
}

.skills .skills__left .skills__box .html, .skills .skills__left .skills__box .css {
  width: 80%;
}

.skills .skills__left .skills__box .javascript {
  width: 78%;
}

.skills .skills__left .skills__box .nodejs {
  width: 75%;
}

.skills .skills__left .skills__box .react {
  width: 78%;
}

.skills .skills__left .skills__box .typescript {
  width: 70%;
}

.skills .skills__right {
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
  padding: 2rem 6em;
  display: flex;
  box-shadow: 0 0 1rem 1px #0000001a;
}

@media only screen and (width <= 35.8em) {
  .skills .skills__right {
    display: none;
  }
}

.skills .skills__right img {
  max-width: 100%;
  max-height: 100%;
}

@media only screen and (width <= 50em) {
  .skills .skills__right img {
    max-width: 200px;
  }
}

.contact {
  grid-column: center-start / center-end;
  margin: 2rem 0 10rem;
}

@media only screen and (width <= 35.8em) {
  .contact {
    margin: 0;
  }
}

.contact__container {
  background-color: #fff;
  border-radius: 1rem;
  grid-template-columns: 1fr 2fr;
  width: 100%;
  max-width: 82rem;
  margin: 0 auto;
  display: grid;
  box-shadow: 0 0 2rem 1px #0000001a;
}

@media only screen and (width <= 50em) {
  .contact__container {
    grid-template-columns: 1fr;
  }
}

.contact__info {
  text-align: center;
  flex-direction: column;
  flex: 100%;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  font-size: 1rem;
  display: flex;
}

.contact__details {
  width: 100%;
  padding: 1rem;
}

.contact__details h2, .contact__details h3 {
  color: #3c4858;
}

.contact__details svg {
  fill: #ff4d79;
  width: 3rem;
  height: 3rem;
}

.contact__form {
  background-color: #ff4d79;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  display: grid;
  position: relative;
}

.contact__form h1 {
  color: #fff;
  margin-bottom: 1rem;
  font-family: inherit;
  font-size: 2rem;
}

.contact__form .circle {
  background: linear-gradient(135deg, #0000 20%, #f36);
  border-radius: 50%;
  position: absolute;
}

.contact__form .circle.one {
  width: 13rem;
  height: 13rem;
  top: 13rem;
  right: -4rem;
}

@media only screen and (width <= 50em) {
  .contact__form .circle.one {
    right: 1rem;
  }
}

.contact__form .circle.two {
  width: 8rem;
  height: 8rem;
  top: 1rem;
  right: 3rem;
}

.contact__form:before {
  content: "";
  background-color: #ff4d79;
  width: 26px;
  height: 26px;
  position: absolute;
  top: 5rem;
  left: -1.3rem;
  transform: rotate(45deg);
}

@media only screen and (width <= 50em) {
  .contact__form:before {
    display: none;
  }
}

.contact__form textarea {
  resize: none;
  border-radius: 22px;
  min-height: 200px;
  padding: .8rem 1.2rem;
  overflow-y: auto;
}

.contact__input {
  border: 2px solid #fff;
  border-radius: .5rem;
  outline: none;
  width: 100%;
  margin-bottom: 1.5rem;
  padding: .8rem .4rem;
  font-size: 1.5rem;
  font-weight: 500;
}

.contact__button {
  color: #f36;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 25px;
  outline: none;
  padding: 1.5rem 4rem;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1;
  transition: all .3s;
}

.contact__button:hover {
  background-color: var(--color-grey-light-2);
  color: var(--color-primary-dark);
  box-shadow: var(--shadow-dark);
  font-weight: 500;
}

.work {
  background-color: var(--background-color);
  grid-column: full-start / full-end;
  margin-top: -4.5rem !important;
}

.work .container {
  place-content: center;
  width: 100%;
  margin-bottom: 1rem;
  padding: 5rem 15rem;
  display: flex;
}

@media only screen and (width <= 75em) {
  .work .container {
    padding: .5rem 1rem;
  }
}

.work__image {
  border-radius: 1rem;
}

.work .btn__container {
  margin: -5.5rem 1.5rem 1.5rem;
  display: flex;
}

.work__btn {
  background-color: var(--color-grey-light-3);
  z-index: 2;
  border: none;
  border-radius: 10px;
  outline: none;
  margin-right: 1rem;
  padding: .2rem 1.1rem;
}

.work__btn a {
  color: var(--color-secondary);
  justify-content: start;
  align-items: center;
  transition: background-color .5s;
  display: flex;
}

.work__btn svg {
  fill: var(--color-secondary);
  width: 3rem;
  height: 3rem;
  padding-right: 1rem;
}

.glide__slide {
  padding: 0 1.5rem;
}

.glide__arrow {
  color: var(--color-primary-light) !important;
  background-color: #f2f1ef1a !important;
}

@media only screen and (width <= 35.8em) {
  .glide__arrow {
    padding: 4px 7px !important;
    font-size: 1.2rem !important;
  }

  .glide__arrow--right {
    right: -.5em !important;
  }

  .glide__arrow--left {
    left: -.5em !important;
  }
}

.footer {
  color: #fff;
  text-align: center;
  background-color: #0e2431;
  grid-column: full-start / full-end;
  padding: 2rem 0;
  font-weight: 500;
}

.footer__title a {
  text-transform: uppercase;
  color: #fff;
  font-size: 3rem;
}

.footer__copy {
  font-size: 1.3rem;
}

.footer__social {
  justify-content: center;
  justify-items: center;
  display: flex;
}

.footer__social li:not(:last-child) {
  padding-right: 2rem;
}

.footer__social svg {
  fill: #fff;
  width: 1.5rem;
  height: 1.5rem;
}

.mb-1 {
  margin-bottom: 1.5rem;
}

.btn {
  text-transform: uppercase;
  background-image: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark));
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  font-size: 1.5rem;
  font-weight: 300;
  position: relative;
  overflow: hidden;
}

.btn > * {
  width: 100%;
  height: 100%;
  transition: all .2s;
  display: inline-block;
}

.btn__visible {
  padding: 1.4rem 4rem;
}

.btn__invisible {
  padding: 2rem 0;
  font-size: 1.2rem;
  position: absolute;
  top: -100%;
  left: 18px;
}

@media only screen and (width <= 950px) {
  .btn__invisible {
    left: 12px;
  }
}

.btn:hover {
  background-image: linear-gradient(to left, var(--color-primary-light), var(--color-primary-dark));
  animation: 1s infinite pulsate;
}

.btn:hover .btn__visible {
  transform: translateY(100%);
}

.btn:hover .btn__invisible {
  top: -3px;
}

.btn:focus {
  outline: none;
  animation: 1s infinite pulsate;
}

@keyframes pulsate {
  0% {
    box-shadow: none;
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
    box-shadow: 0 1rem 4rem #00000040;
  }

  100% {
    box-shadow: none;
    transform: scale(1);
  }
}

